<template>
  <v-container fluid class="pa-2">
    <v-row no-gutters>
      <v-col cols="12" class="mb-2">
        <h1>ITEM LIST   </h1>
      </v-col>
      <v-col cols="12" class="mb-5">
        <v-expansion-panels>
          <v-expansion-panel>
            <v-expansion-panel-title></v-expansion-panel-title>
            <v-expansion-panel-text>
              Quest Live<br>
              
            </v-expansion-panel-text>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>

    <!--<v-row
      no-gutters
      v-for="arr in [{id:1,label:''}, {id:2,label:''}, {id:3,label:''}]"
      :key="arr"
    >
      <v-col cols="12" class="mb-5">
        <v-select
          v-model="select['item' + arr.id].value"
          :items="select['item' + arr.id].item"
          chips
          :label="`(${arr.label})`"
          multiple
          clearable
          color="pink"
          base-color="pink"
          :hint="`${arr.label}`"
          persistent-hint
        ></v-select>
      </v-col>
    </v-row>-->

    <v-row no-gutters>
      <!--<v-col cols="6">
        <v-select
          v-model="select.season.value"
          :items="select.season.item"
          attach
          chips
          label="Select Season"
          multiple
          clearable
        ></v-select>
      </v-col>
      <v-col cols="6">
        <v-select
          v-model="select.area.value"
          :items="select.area.item"
          attach
          chips
          label="Select Area"
          multiple
          clearable
        ></v-select>
      </v-col>-->
      <v-col
        v-for="(v, i) in ['', '', '']"
        :key="i"
        cols="12"
        class="mb-5"
      >
        <v-select
          v-model="select[`item${i + 1}`].value"
          :items="select[`item${i + 1}`].item"
          attach
          chips
          :label="`(${v})`"
          multiple
          clearable
          color="pink"
          base-color="pink"
          variant="outlined"
          :hint="`${v}`"
          persistent-hint
        >
          <template v-slot:chip="{ item }">
            <v-chip
              v-if="item.title !== '-'"
              pill
              class="pl-0"
              :color="searchColor(item.title)"
            >
              <v-avatar left class="mr-1">
                <v-img
                  :src="require(`@/assets/trainingItem_icon/${item.title}.png`)"
                ></v-img>
              </v-avatar>
              {{ item.title }}
            </v-chip>
            <v-chip v-else>{{ item.title }}</v-chip>
          </template>
          <template v-slot:item="{ item }">
            <v-list-item
              :title="item.title"
              @click="selectItem(item.title, i + 1)"
            >
              <template v-slot:prepend>
                <v-checkbox-btn
                  color="pink"
                  :model-value="select[`item${i + 1}`].value.some((elm) => elm === item.title)"
                ></v-checkbox-btn>
                <v-img
                  v-if="item.title !== '-'"
                  :src="require(`@/assets/trainingItem_icon/${item.title}.png`)"
                  :alt="item.title"
                  class="mr-2"
                  style="width: 40px"
                ></v-img>
              </template>
            </v-list-item>
          </template>
        </v-select>
      </v-col>
    </v-row>

    <v-row no-gutters>
      <v-col cols="12">
        <v-data-table
          :headers="headers"
          :items="filterItems"
          item-key="name"
          density="comfortable"
          items-per-page="75"
          items-per-page-text="1"
          :items-per-page-options="[
            {value: 15, title: '15'},
            {value: 30, title: '30'},
            {value: 45, title: '45'},
            {value: 60, title: '60'},
            {value: 75, title: '75'},
            {value: -1, title: '$vuetify.dataFooter.itemsPerPageAll'}
          ]"
          multi-sort
          no-data-text=""
        >
          <template v-slot:item="{ item }">
            <tr>
              <td>{{ item.name }}</td>
              <td>{{ item.area }}</td>
              <td>{{ item.stage }}</td>
              <td
                v-for="i in 3"
                :key="i"
              >
                <v-chip
                  v-if="item[`${i}`] !== '-'"
                  pill
                  class="pl-0"
                  :color="searchColor(item[`${i}`])"
                >
