                      :src="require(`@/assets/trainingItem_icon/${item[`${i}`]}.png`)"
                    ></v-img>
                  </v-avatar>
                  {{ item[`${i}`] }}
                </v-chip>
                <v-chip v-else>{{ item[`${i}`] }}</v-chip>
              </td>
            </tr>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

