  computed: {
    filterItems() {
      const _this = this;
      let result = _this.allItemList;

      for (let i = 1; i <= 3; i++) {
        result = ((i) => {
          if (_this.select['item' + i].value.length > 0 && _this.select['item' + i].value.length < _this.select['item' + i].item.length) {
            const regex = new RegExp(_this.select['item' + i].value.join('|').replace(/\(/g, '\\(').replace(/\)/g, '\\)'))
            return result.filter(arr => regex.test(arr['' + i]));
          } else {
            return result;
          }
        })(i);
      }

      _this.setLocalStrage();
      return result;
    }
  },
  created() {
    for (const season in this.list) {
      for (const area in this.list[season]) {
        this.allItemList = this.allItemList.concat(this.list[season][area]);
      }
    }

    if (localStorage.llllMgr_selectItemList !== undefined) {
      const getSelectItemList = JSON.parse(localStorage.llllMgr_selectItemList);

      for (let i = 1; i <= 3; i++) {
        this.select['item' + i].value = getSelectItemList['item' + i];
      }
    }
  },
  methods: {
    setLocalStrage() {
      const setLocalStrageDataList = {
        item1: this.select.item1.value,
        item2: this.select.item2.value,
        item3: this.select.item3.value
      };
      
      localStorage.llllMgr_selectItemList = JSON.stringify(setLocalStrageDataList);
    },
    searchColor(target) {
      let color;

      for (const itemName in this.colorList) {
        if (target.includes(itemName)) {
          color = this.colorList[itemName];
          break;
        }
      }

      return color;
    },
    selectItem(selector, i) {
      if (this.select[`item${i}`].value.some((x) => x === selector)) {
        const result = [];

        for (const skill of this.select[`item${i}`].value) {
          if (skill !== selector) {
            result.push(skill);
          }
        }

        this.select[`item${i}`].value = result;
      } else {
        this.select[`item${i}`].value.push(selector);
      }
    }
  }
};
</script>
